@use postcss-cssnext;

body {
  background-color: #1e2931;
  color: #fff;
  font-family: Verdana, sans-serif;
  font-style: normal;
  line-height: 1.5;
  margin: 0 auto;
  width: 90%;
}

figure {
  margin: 0;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

input {
  border: 0;
  font: inherit;
  margin: 0;
  padding: 0;
}

.search {
  font-size: 1.5rem;
  margin: 1.5em auto 2em;
  width: 80vw;
}

.search [type='search'] {
  background-color: #ffffff;
  color: #898F9C;
  padding: 0.5em 2em;
  text-align: center;
  width: 100%;
}

.movies {
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(auto-fill, minmax(10em, 1fr));
  list-style: none;
  margin: 0;
  padding: 0;
}

.movies > li {
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.3);
}

.figcaption {
  padding: 1em;
  display:none;
}

.movie__title {
  font-size: 12px;
  font-weight: normal;
  font-variant: small-caps;
  margin: 0;
  width:100%;
  padding: 5px 0px 10px 10px;

}

.footer {
  background-color: black;
  color: gray;
  line-height: 20px;
  padding-top: 20px;
  padding-bottom: 6px;
  width: 100%;
  bottom: 0;
  text-align: center;
  font-family: Sans-serif;
}  
